<script>
  import Reveal from 'reveal.js/js/reveal'
  import dataViz from '@/components/dataViz_uv'
  import uv from './Measure_uv'
  import dataCap from '@/components/dataCapture_uv'

  export default{
    data() {
      return {
        learn_radio: '',
        learn_text: 'There are some knowledge I want to tell you before start the experiment...',
        checklist: [],
        radio: '',
        input_reason: '',
        input_evidence: '',
        input_finding: '',
        uvData: null,
        luxData: null,
        reData: null,
        multi_able: true,
        radio_able: true,
        next_able: true,
      }
    },
    components: {
      'dataViz': dataViz,
      'uv': uv,
      'dataCap': dataCap,
    },
    mounted() {
      Reveal.initialize({
        keyborad: false,
        controls: false,
        overview: false,
        touch: false
      });
      Reveal.addEventListener('hypo1', function() {
        console.log('here');
      });
    },
    methods: {
      Next() {
        Reveal.right();
      },
      Left() {
        Reveal.left();
      },
      Down() {
        Reveal.down();
      },
      Jump(h,v) {
        Reveal.slide(h,v);
      },
      muvData(uv) {
        this.uvData = uv;
      },
      mluxData(lux) {
        this.luxData = lux;
      },
      resultData(rd) {
        this.reData = rd;
      },
      Export() {
        window.print();
      }
    },
    watch: {
      learn_radio() {
        console.log(this.learn_radio);
        if (this.learn_radio == 1 || this.learn_radio == 2) {
          this.learn_text = 'Please read these two document before start your experiment'
        }
        Reveal.right();
      },
      reData() {
        if (this.reData != null) {
          this.next_able = false;
        }
      },
      checklist() {
        if (this.checklist.length == 3) {
          this.multi_able = false;
        }
      },
      radio() {
        if (this.radio != '') {
          this.radio_able = false;
        }
      }
    }
  }
</script>

